    font-family: Roboto;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

:root {
    font-size: 62.5%;
    font-family: Roboto, sans-serif;
    --light-bg-colour: #bfdbfe;
    --medium-bg-colour: #2563eb;
    --dark-bg-colour: #1e3a8a;
    --task-bg-color: rgba(30, 58, 138, 0.4);
    --primary-text-colour: #f8fafc;
    --secondary-text-colour: #f1f5f9;
    --hover-text-colour: #cbd5e1;
    --section-width: 300px;
}

body {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px;
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-rows: 1.5fr 12fr 0.5fr;
}

header {
    display: grid;
    grid-template-columns: 2fr 2fr 1fr 1fr;
    align-items: center;
    justify-items: center;
    padding: 10px;
    font-size: 2.5rem;
    font-weight: bold;
    background-color: var(--medium-bg-colour);
    color: var(--secondary-text-colour);
    letter-spacing: 0.2rem;
    box-shadow: 0px 1px 10px var(--dark-bg-colour);
    z-index: 2;
}

#new-project:hover, #projects:hover, #projects-drop-down li:hover {
    cursor: pointer;
    color: var(--hover-text-colour);
}

#projects-drop-down {
    visibility: hidden;
    position: absolute;
    top: 80px;
    padding: 15px;
    font-size: 1.5rem;
    min-width: 10%;
    background-color: var(--dark-bg-colour);
    border: 2px solid var(--secondary-text-colour);
}

#projects-drop-down.visible {
    visibility: visible;
}

#projects-drop-down > ul {
    list-style: none;
    margin: 0px;
    padding: 0px;
}

#projects-drop-down li {
    margin-bottom: 10px;
}

#projects-drop-down li:last-child {
    margin-bottom: 0px;
}

.content {
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: minmax(var(--section-width), 0.1fr);
    overflow-x: auto;
    column-gap: 2%;
    background-color: var(--light-bg-colour);
    padding: 20px;
}

.section {
    border: 1px solid black;
    padding: 15px;
    background-color: #dbeafe;
    border-radius: 15px;
}

.add-section {
    border: 2px dashed black;
    padding-top: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 15px;
}

.add-section > h2 {
    margin: 0px;
}

.add-section > button {
    width: 25%;
    padding: 0px;
    margin: 0px;
    border: none;
    background-color: var(--light-bg-colour);
}

.add-section > button > img:hover {
    cursor: pointer;
    filter: invert(16%) sepia(83%) saturate(2018%) hue-rotate(216deg) brightness(91%) contrast(94%);
}

.section-title {
    display: flex;
    align-items: center;
    justify-content: center;
}

.button-container {
    display: flex;
}

.button-container > button {
    height: 35px;
    width: 35px;
    padding: 0px;
    margin: 0px;
    border: none;
    background-color: #dbeafe;
}

.button-container > button > img:hover {
    cursor: pointer;
    filter: invert(16%) sepia(83%) saturate(2018%) hue-rotate(216deg) brightness(91%) contrast(94%);
}

.section-title > h2 {
    flex-grow: 1;
    text-align: center;
    font-size: 2.5rem;
    letter-spacing: 0.1rem;
    padding: 0px;
    margin: 10px;
}

.section-tasks {
    display: grid;
    grid-auto-rows: minmax(100px, 1fr);
    row-gap: 20px;
}

.task {
    border: 1px solid black;
    background-color: var(--task-bg-color);
    padding: 5px 15px;
    text-align: center;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    border-radius: 10px;
    box-shadow: 1px 1px var(--dark-bg-colour);
}

.task.completed {
    opacity: 50%;
}

.task:hover {
    cursor: pointer;
    outline: 2px solid var(--dark-bg-colour);
}

.task > h3 {
    font-size: 1.8rem;
    margin: 10px 0px;
    overflow-wrap: break-word;
}

.task > p {
    font-size: 1.5rem;
    margin: 10px 0px;
}

.task-toolbar { 
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.task-toolbar > button {
    height: 30px;
    width: 30px;
    padding: 0px;
    margin: 0px;
    border: none;
    background-color: rgba(30, 58, 138, 0);;
}

.task-toolbar > button:hover {
    cursor: pointer;
    filter: invert(16%) sepia(83%) saturate(2018%) hue-rotate(216deg) brightness(91%) contrast(94%);
}

input[type=checkbox] {
    transform: scale(1.5);
}

.task-toolbar > input:hover {
    cursor: pointer;
}

.priority-indicator {
    flex-basis: 50%;
    height: 80%;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    color: var(--secondary-text-colour);
}

footer {
    padding: 10px 30px;
    font-size: 2rem;
    color: var(--primary-text-colour);
    background-color: var(--dark-bg-colour);
    box-shadow: 1px 0px 10px var(--medium-bg-colour);
}

.dev-details {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 15px;
    height: 100%;
}

footer img {
    height: 30px;
}


dialog[open] > form {
    display: grid;
    grid-template-columns: 1fr 3fr;
    align-items: center;
    padding: 10px;
    gap: 15px;
}

dialog[open] > form > h2 {
    grid-column: 1 / span 2;
    justify-self: center;
    font-size: 2.5rem;
}

dialog[open] > form > label {
    justify-self: end;
    font-size: 2rem;
}

dialog[open] > form > input, dialog[open] > form > textarea, dialog[open] > form > select {
    font-size: 2rem;
}

.form-buttons {
    grid-column: 1 / span 2;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10%;
    margin-top: 15px;
}

.form-buttons > button, #task-view > button {
    font-size: 2rem;
    padding: 10px 30px;
    background-color: var(--dark-bg-colour);
    color: var(--primary-text-colour);
    border-radius: 5px;
    border: 1px solid var(--medium-bg-colour);
}

.form-buttons > button:hover, #task-view > button:hover {
    outline: 4px solid var(--light-bg-colour);
    color: var(--secondary-text-colour);
    cursor: pointer;
}

#task-view[open], #task-checklist[open] {
    display: flex;
    flex-direction: column;
    max-height: 90%;
    max-width: 50%;
}

#task-view[open] button {
    width: 150px;
    min-width: fit-content;
    align-self: center;
    margin: 5px;
}

.task-details, .checklist-details {
    min-width: 300px;
    display: flex;
    flex-direction: column;  
}

.task-details {
    overflow: overlay;
}

.task-details > h3 {
    font-size: 3rem;
    text-align: center;
    margin: 10px;
}

.task-details > p {
    font-size: 2rem;
    margin: 10px;
}

.task-details > .description {
    border: 1px solid var(--dark-bg-colour);
    padding: 15px;
    white-space: pre-wrap;
    background-color: var(--light-bg-colour);
    overflow: auto;
    max-height: 50vh;
}

/* Drop down menu styling*/
.relative-position-container {
    position: relative;
}

.menu-button {
    height: 35px;
    width: 35px;
    border: none;
    background: none;
}

.menu-button, .menu-items {
    padding: 0px;
    margin: 0px;
}

.menu-button:hover {
    cursor: pointer;
    filter: invert(16%) sepia(83%) saturate(2018%) hue-rotate(216deg) brightness(91%) contrast(94%);
}

.menu-container {
    visibility: hidden;
    position: absolute;
    top: 35px;
    padding: 15px;
    z-index: 1;
    background-color: var(--dark-bg-colour);
    border: 2px solid var(--secondary-text-colour);
}

.menu-container.visible {
    visibility: visible;
    white-space: nowrap;
}

.menu-items {
    list-style: none;
}

.menu-item {
    font-size: 1.5rem;
    color: var(--primary-text-colour);
    padding: 2.5px;
    display: flex;
    align-items: center;
}

.menu-item:hover {
    cursor: pointer;
    color: var(--hover-text-colour);
}

.section-button {
    border: none;
    background: none;
}

.section-button:hover {
    cursor: pointer;
}

.section-button > img {
    height: 1.5rem;
    filter: invert(93%) sepia(77%) saturate(7263%) hue-rotate(181deg) brightness(125%) contrast(100%);
}

.checklist-details {
    font-size: 2rem;
    list-style: none;
    margin: 0px;
    padding: 20px;
}

.create-checklist-item-container {
    display: flex;
    justify-content: center;
}

.checklist-item-container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 8px;
}

.create-checklist-item-container > input {
    font-size: 2rem;
    width: 80%;
}

